.main-nav__nav {
  text-align: center;
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  border: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:active {
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle {
  top: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 31px;
  left: 25px;
  width: 26px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #000000;
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle .main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--opened .main-nav__toggle .main-nav--nojs .main-nav__nav {
  position: static;
  min-height: 0;
}
.main-nav--opened .main-nav__toggle .main-nav--nojs .main-list__item {
  display: block;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  left: 26px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
.main-nav__button {
  display: block;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin-top: 13px;
}
.main-nav__item {
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  list-style-type: none;
  padding-top: 22px;
  padding-bottom: 22px;
  border-bottom: 1px solid #D9D9D9;
}
.main-nav__link {
  text-decoration: none;
  font-family: "Oswald", "Tahoma", sans-serif;
  color: #000000;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__nav {
    display: flex;
  }
  .main-nav__button {
    display: none;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    width: 441px;
    border-bottom: none;
    margin-top: 0;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__item {
    position: relative;
    border-bottom: none;
  }
  .main-nav__item:hover {
    opacity: 0.6;
  }
  .main-nav__item:active {
    opacity: 0.3;
  }
  .main-nav__item .main-nav__link {
    border-bottom: none;
    font-size: 17px;
  }
  .main-nav__item .main-nav__link--active::after {
    content: "";
    position: absolute;
    bottom: 0;
    top: 51px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    width: 479px;
  }
