    opacity: 0.6;
  }
  .main-nav__item:active {
    opacity: 0.3;
  }
  .main-nav__item .main-nav__link {
    border-bottom: none;
    font-size: 19px;
  }
  .main-nav__item .main-nav__link--active::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #68b738;
  }
  .main-nav__item .main-nav__link--index {
    color: #ffffff;
    /*border-bottom: 2px solid @basic-white;*/
  }
}
